<template>
  <div class="bg">
    <div class="title">投资案例</div>
    <article class="person_main">
      <div>
        <div>
          <DatePicker
            v-model="formValidate.time"
            type="date"
            placeholder="选择时间"
            :editable="false"
            value="yyyy-MM-dd HH:mm:ss"
            @on-change="formValidate.time=$event;"
            style="width: 200px;margin-right:20px;float: left;"
          ></DatePicker>
          <Input
            v-model="formValidate.projectName"
            placeholder="项目名称"
            style="width: 200px; margin-right: 10px;float: left;"
          />
          <Input
            v-model.number="formValidate.money"
            placeholder="金额"
            style="width: 200px;margin-right: 10px;float: left;"
          >
            <span slot="append">万元</span>
          </Input>
        </div>
        <div style="clear: both;margin-bottom: 10px;"></div>
        <div>
          <Button type="primary" @click="clickAdd">添加新案例</Button>
        </div>
        <Divider />
      </div>
      <div style="clear: both;"></div>
      <div style="margin-top:30px;">
        <Timeline>
          <Timeline-Item v-for="(item, index) in data" :key="index">
            <p class="Titime">投资时间：{{item.time}}</p>
            <p class="Tititle">投资项目名称：{{item.projectName}}</p>
            <p class="Ticontent">总投资：{{item.money}}万元</p>
            <p class="Ticontent">
              <Poptip confirm title="是否要删除资金案例" @on-ok="clickShow(item.id)">
                <Button type="error">删除案例</Button>
              </Poptip>
            </p>
          </Timeline-Item>
        </Timeline>
      </div>
    </article>
  </div>
</template>
<script>
import { fileUploadUrl, imgUrl } from "../../../utils";
import { investmentAdd,investments,investmentEdit } from "../../../api/capital";
import { messages } from "../../../utils";
import {
  Timeline,
  Button,
  TimelineItem,
  Poptip,
  Upload,
  Divider,
  DatePicker,
  Input
} from "iview";
export default {
  components: {
    [Input.name]: Input,
    [Timeline.name]: Timeline,
    [TimelineItem.name]: TimelineItem,
    [Poptip.name]: Poptip,
    [Upload.name]: Upload,
    [Divider.name]: Divider,
    DatePicker,
    [Button.name]: Button
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        pageNo: 1,
        pageTotal: 1
      },
      formValidate: {
        code: this.$route.params.code,
        time: "",
        money: "",
        projectName: ""
      },
      data: []
    };
  },
  methods: {
    // 添加进度
    clickAdd() {
      investmentAdd(this.formValidate).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`进度添加成功`);
          //将文件上传复位
          this.formValidate = {
            code: this.$route.params.code,
            time: "",
            money: "",
            projectName: ""
          };
          // 重新获取列表
          this.getData();
        }
      });
    },
    //获取数据
    getData() {
      investments({
        code: this.$route.params.code,
        pageNo: this.seachConfig.pageNo
      }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.data = res.data.data.rows;
          this.seachConfig.pageTotal = res.data.data.total;
        }
      });
    },
    //设置隐藏
    clickShow(id) {
      investmentEdit({ id }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`删除成功`);
          this.getData();
        }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
